:root {
  --color-main: #308ca9;
  --color-main2: rgb(0, 185, 185);
  --color-main3: rgb(53, 168, 168);

  --color-container: hsl(187, 63%, 34%);
  --color-container2: #20605d;
  --color-container3: #3dd2ff;

  --color-green: #36f936;
  --color-red: rgb(233, 233, 233);
  --color-yellow: yellow;
  --color-yellow2: rgb(255, 255, 160);
  --med: 10px 10px;
}

* {
  padding: 0;
}

body {
  background-color: var(--color-red);
  overflow-x: hidden;
}

.container {
  margin: 2em;
  display:grid;
  grid-template-columns: repeat(auto-fill,minmax(190px,1fr));
  grid-gap: 1em;
}
.anime {
  width: 100%;
  height: 100%;
  background-color: var(--color-main2);
  border-radius: 10px;
  border: 1px solid black;
}
.green,
.red,
.yellow {
  font-weight: 900;
}
.green {
  color: var(--color-green);
}
.red {
  color: var(--color-red);
}
.yellow {
  color: var(--color-yellow);
}
h2,
h3,
p,
li {
  color: black;
  font-family: Roboto;
}

h1 {
  font-size: 0.8rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
  font-family: Roboto, Ubuntu;
  color: var(--color-yellow);
}
.img {
  object-fit:cover;
  height: 20em;
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.a,.b
.img {
  cursor: pointer;
}
input,
.input-container {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20rem;
  border-radius: 10px;
  height: 2rem;
}

.a,
.b{
  height: 2rem;
  width: 100%;
  font-weight: 1000;
  text-align: center;
  border: solid 1px;
}
.input-container{
padding:1rem;
background-color: aqua;
}
.a {
  background-color: var(--color-yellow);
}
.b{
  background-color: var(--color-yellow);
}
button:hover {
  background-color: var(--color-yellow2);
  transition: 1s;
  color: var(--color-main);
}
.answer {
  color: #fff;
  padding: 1rem;
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s cubic-bezier(0, 1, 0, 1);
}
.answer.show {
  height: auto;
  max-height: 99999px;
  transition: all 1s cubic-bezier(1, 0, 1, 0);
}

@media screen and (max-width: 800px) {
  .anime {
    flex-wrap: wrap;
    width: 70vw;
    align-items: center;
    overflow: hidden;
    justify-content: center;
    height: auto;
  }
}
